export default {
	data() {
		return {
			customTheme: '',
			commentContent: "",
			article: ''
		}
	},
	onLoad(e) {
		this.customTheme = this.$store.state.customTheme
		this.$http({
			url: '/article/' + e.id,
		}).then(res => {
			res.content = res.content.replace(/\<img/gi, '<img style="width:100%;height:auto" ')
			this.article = res
		})
	},
	onReady() {

	},
	methods: {
		previewImage(e) {},
		scrollTop() {
			uni.pageScrollTo({
				scrollTop: 0,
				duration: 300
			});
		}
	}
}
